//@use "src/assets/styles/utils";
@import "../../assets/styles/constants";

$size_brandCircle: 136px;

.header {
  background: var(--uk-kit-colors-menuPrimaryBg);
  display: flex;
  width: 100%;
  height: $size_headerHeight;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 100;
}

.endContent {
  height: 100%;
  position: relative;
}

.logoContainer {
  display: flex;
  align-items: center;
  margin-left: 30px;
}

.emptySpace {
  flex-grow: 1;
}

.link {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 19px;
  z-index: 1;
}
