@import './src/components/Header/Header.module';
@import '../../assets/styles/constants';

.wrapper {
  position: relative;
}

.select {
  height: $size_headerHeight;
  display: flex;
  justify-content: flex-end;
  padding: 18px 10px;
  cursor: pointer;
  transition: background-color $animation_easeInOut $animation_speedNormal;

  &_active {
    background-color: var(--uk-kit-colors-bgMenuSecondary);
  }
}

.arrowIcon {
  margin-left: 3px;
  transition: transform $animation_easeInOut $animation_speedNormal;

  &_rotated {
    transform: rotate(180deg);
  }
}

.languageList {
  position: absolute;
  top: 100%;
  right: 0;
  padding: 30px;
  background-color: var(--uk-kit-colors-bgMenuSecondary);
  width: 232px;

  @media all and (max-width: $screen_md_min)  {
    padding: 20px;
  }
}

.languageRow {
  display: flex;
  align-items: center;
  cursor: pointer;

  &:not(:last-child) {
    margin-bottom: 10px;
  }

  .languageRowIcon {
    margin-right: 8px;
  }
}
