@use '../../assets/styles/functions';
@import '../../assets/styles/constants';

.wrapper {
  background-color: var(--uk-kit-colors-brandTertiary);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.main {
  margin-top: #{functions.cssVar(size, headerHeight)};
  position: relative;
  width: 100%;
  min-height: calc(100vh - #{functions.$size_headerHeight} - #{functions.$size_footerHeight});
}

.message {
  margin: $margin_xl auto 24px auto;
  width: 396px;
  padding: 12px 20px;
  text-align: center;
  z-index: 1;
  border-radius: $borderRadius_l;

  &.success {
    background-color: var(--uk-kit-colors-actionConfirmSecondary);
  }
  &.warning {
    background-color: var(--uk-kit-colors-actionPromoSecondary);
  }
  &.error {
    background-color: var(--uk-kit-colors-actionErrorSecondary);
  }
  &.info {
    background-color: var(--uk-kit-colors-brandQuarternary);
  }

  @media screen and (max-width: $screen_sm_max) {
    margin: $margin_xl $margin_s 24px $margin_s;
    width: auto;
  }
}

.card {
  margin: 0 auto;
  box-shadow: $shadow_variant-3;
  padding: $margin_xxl $margin_xl;
  width: 470px;
  border-radius: $borderRadius_l;
  background-color: var(--uk-kit-colors-bgPrimary);
  margin-top: $margin_xl;

  &.account {
    max-width: none;
  }
  &.message {
    margin-top: 0;
  }

  @media all and (max-width: $screen_sm_max) {
    margin-left: $margin_s;
    margin-right: $margin_s;
    width: auto;
    padding: $margin_l $margin_s;
  }
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.usernameNode {
  margin-top: $margin_s;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.link {
  cursor: pointer;
  margin-left: $margin_s;
}

.footer {
  text-align: center;
  height: $size_footerHeight;
}

.logo {
  bottom: 10px;
  opacity: 0.3;
}

.marginTop {
  margin-top: $margin_s;
}
