@use '../../assets/styles/pages.module';
@import '../../assets/styles/constants';

.htmlList {
  text-align: left;
  margin-top: $margin_l;
  ol > li::marker {
    font-weight: 400;
    font-size: 15px;
  }
  ul > li {
    list-style-type: none;
    margin-left: $margin_s;
  }
  ul > li:nth-child(n+2) {
    margin-top: 10px;
  }
}

.margin {
  margin-left: $margin_s;
}

.center {
  text-align: center;
}
