// Animations
$animation_easeInOut: ease-in-out;
$animation_speedNormal: 0.2s;
$animation_speedFast: 0.1s;

// Breakpoints
$screen_xs_min: 320px;
$screen_sm_min: 485px;
$screen_sm_max: 600px;
$screen_md_min: 768px;
$screen_md_max: 960px;
$screen_lg_min: 1024px;
$screen_xl_min: 1200px;

// Sizes
$size_headerHeight: 60px;
$size_footerHeight: 55px;

// Prefix
$kcCustomVariablePrefix: --kc-custom;

// Shadows
$shadow_variant-1: 0px 3px 12px rgba(0, 0, 0, 0.18);
$shadow_variant-2: 0px 2px 3px rgba(0, 0, 0, 0.1);
$shadow_variant-3: 0px 100px 80px rgba(0, 0, 0, 0.07), 0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275), 0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725);

// Border-radius
$borderRadius_none: 0;
$borderRadius_s: 4px;
$borderRadius_m: 6px;
$borderRadius_l: 8px;
$borderRadius_xl: 12px;
$borderRadius_xxl: 40px;

// Margins
$margin_s: 15px;
$margin_m: 30px;
$margin_l: 40px;
$margin_xl: 60px;
$margin_xxl: 80px;
