@use '../../assets/styles/pages.module';
@import '../../assets/styles/constants';

.socialList {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: $margin_l $margin_s 0 $margin_s;
  justify-content: space-evenly;
}

.socialItem {
  box-shadow: $shadow_variant-2;
  background-color: var(--uk-kit-colors-bgSecondary);
  margin: 10px;
  padding: 10px;
  cursor: pointer;

  &:hover {
    background-color: var(--uk-kit-colors-bgTertiary);
  }
}
