@import './constants';

.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;

  &.row {
    flex-direction: row;
  }
}

.list {
  display: flex;
  flex-direction: column;
  margin-top: $margin_l;
  text-align: left;
  word-break: break-word;

  > :nth-child(n+2) {
    margin-top: $margin_s;
  }

  &.row {
    flex-direction: row;
  }

  &.center {
    align-items: center;
  }
}

.button {
  margin-top: 0;
}

.htmlList {
  text-align: left;
  margin-top: $margin_l;
  & > li {
    list-style-type: none;
    margin-left: $margin_s;
  }
  & > li:nth-child(n+2) {
    margin-top: 10px;
  }
}
