@use '../../assets/styles/pages.module';
@import '../../assets/styles/constants';

.methodItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  border-radius: $borderRadius_m;
  padding: $margin_s;
  &:hover {
    background-color: var(--uk-kit-colors-bgTertiary);
  }
}

.methodContent {
  display: flex;
  flex-direction: column;
  > :nth-child(n+2) {
    margin-top: 10px;
  }
}
