@use '../../assets/styles/pages.module';
@import '../../assets/styles/constants';

.list {
  display: flex;
  flex-direction: column;
  margin-top: $margin_l;

  > :nth-child(n+2) {
    margin-top: $margin_s;
  }

  &.row {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;

    > :nth-child(n+2) {
      margin-top: 0;
    }
  }
}

.card {
  padding: $margin_m;
  border-radius: $borderRadius_xl;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  border: 3px solid var(--uk-kit-colors-interfaceTertiary);
  justify-content: center;
  align-items: center;

  &:hover {
    box-shadow: $shadow_variant-1;
  }

  &.selected {
    border: 3px solid var(--uk-kit-colors-brandTertiary);
  }
}
